import React, { Component } from "react";

import { Textbox, Textarea } from 'react-inputs-validation';
    import "../styles/react-inputs-validation.min.css";
    import Select from 'react-select';
    import $ from 'jquery';
class Firebase extends Component {
    constructor(props) {
        super(props);
        this.state = {
          email: "",
          title: "",
          message: "",
          validate: false
        };
        this.validateForm = this.validateForm.bind(this);
        this.state.emailData = [];
    }
    componentDidMount() {
        
        global.api.getEmailList(global.companyCode)
        .then(response => response)
        .then(
        data => {
        this.setState({ emailData: data});
        });
    }
    onEmailSelect = email => {
        this.setState({ email });
        console.log(`Option selected:`, email);
        
        let value = [];
        if(email !== null){
          for (var i = 0; i < email.length; i++) {
              value.push(email[i]);
          }
        }
        this.setState({email: value}); 
      };
    toggleValidating(validate) {
        this.setState({ validate });
    }
    validateForm(e) {
        e.preventDefault();
        this.toggleValidating(true);
        const {
          hasEmailError,
          hasTitleError,
          hasMessageError
        } = this.state;
        if (
          !hasEmailError &&
          !hasTitleError &&
          !hasMessageError 
        ) {
          
        }
        if(this.state.email.length===0){
            this.setState({hasEmailError:'please one email'})
            alert('please select atleast one email')
            return false;
        }else{
            var data = {
                'email':this.state.email,
            'message':this.state.message,
                'title':this.state.title,
                'include_image':$('#include_img').is(":checked"),
            }
            console.log('data',data)
            
        }
      }

  render(){
    const {
        email,
        title,
        message,
        validate,
      } = this.state;
      
    return (
        
        <main className="offset" id="content">
            <link rel="stylesheet" media="screen" href="fonts/flag-icon/css/flag-icon.min.css" />
          <section className="section_box">
            <h4 className="title4 fw500 mb20">Employees</h4>
              <h1 className="title1 mb50">Push Notification</h1>
              <form onSubmit={this.validateForm}  className="form_employee  js-tabs">
                
                <div className="js-tabs-box tab-box">
                  <div>
                    <div className="form_row_box">
                      <h2 className="title2 fw400 form_title mb30">Send to Single Device</h2>
                      <div className="form_row_col">
                        <div className="form_row mb65">
                          <label className="form_label">Select Email</label>
                           <Select  attributesWrapper={{}} attributesInput={{
                            id: "email",
                            name: "email"
                            }} value={email} isMulti onChange={this.onEmailSelect} 
                            onBlur={() => {}}  options={this.state.emailData} className="Select has-value is-clearable is-searchable Select--multi"
                            classNamePrefix="route " closeMenuOnSelect={false}
                            validate={validate} 
                            validationCallback={res =>
                            this.setState({ hasEmailError: res, validate: false })
                            } 
                            validationOption={{
                                name: "Email",
                                check: true, 
                                required: true 
                                }}/> 
                          {/* <Select attributesWrapper={{}} attributesInput={{
                            id: "email",
                            name: "email"
                            }}
                            isMulti
                            classNameSelect="Select has-value is-clearable is-searchable Select--multi"
                            value={email}
                            disabled={false}
                            validate={validate} 
                            validationCallback={res =>
                            this.setState({ hasMovieError: res, validate: false })
                            } 
                            optionList={this.state.emailData} 
                            customStyleOptionListContainer={{
                            maxHeight: "200px",
                            overflow: "auto",
                            fontSize: "14px"
                            }} // Optional.[Object].Default: {}.
                            onChange={(res, e) => {
                            this.setState({ country: res.id });
                            console.log(e);
                            }} 
                            onBlur={() => {}} 
                            
                            validationOption={{
                            name: "Email",
                            check: true, 
                            required: true 
                            }}
                        /> */}
                         
                        </div>
                        {/* <div className="form_row mb65">
                          <label className="form_label">LAST Name</label>
                          <input className="form_input" type="text" id="lname" name="lname" ref="lname"/> 
                        </div> */}
                      </div>
                      <div className="form_row_col">
                        <div className="form_row mb65">
                          <label className="form_label">Title</label>
                          <Textbox classNameInput="form_input" attributesInput={{
                            id: "Title",
                            name: "Title",
                            type: "text",
                            placeholder: "Enter Title"
                            }}
                            value={title}  validationOption={{ name: 'Title',
                           check: true, required: true}}
                           validate={validate} 
                            validationCallback={res =>
                            this.setState({ hasTitleError: res, validate: false })
                            }
                            onChange={(title, e) => {
                                this.setState({ title });
                              }} // Required.[Func].Default: () => {}. Will return the value.
                              onBlur={e => {
                                //console.log(e);
                              }}/>
                          
                        </div>
                        <div className="form_row mb65">
                          <label className="form_label">Message</label>
                          <Textarea classNameInput="form_textarea emalform_textarea" 
                          attributesWrapper={{}}
                          attributesInput={{
                            id: "message",
                            name: "message",
                            placeholder: "Notification Message",
                             cols: '10',
                             rows: '0',
                          }} value={message} placeholder="Notification Message" validationOption={{name: 'Message', // Optional.[String].Default: "". To display in the Error message. i.e Please enter your {name}.
                          check: true, required: true}}
                          validate={validate} 
                          validationCallback={res =>
                            this.setState({
                                hasMessageError: res,
                                validate: false
                            })
                            }
                            onChange={(message, e) => {
                                this.setState({ message });
                              }} // Required.[Func].Default: () => {}. Will return the value.
                              onBlur={e => {
                                //console.log(e);
                              }}/>
                          
                        </div>
                        
                      </div>
                      <div className="form_row_col">
                       
                        <div className="form_row mb65">
                          <label className="form_label" style={{'textTransform':'none'}}>Include Image?</label>
                          <span className="form_label"><input type="checkbox"  id= "include_img"  name="include_img" onChange={( e) => { }}/></span>
                        </div>
                        
                      </div>
                      <div className="">
                  
                  <button className="btn btn-blue btn-radius btn-icon-center btn-size" onClick={this.validateForm}>
                    
                    <span>Send</span>
                  </button>
                  <input type="submit" style={{ display: "none" }} />
                </div>
                    </div>
                  </div>
                  
                </div>
                
              </form>
            </section>
      </main>
    )
  }
}
export default Firebase;

